module.exports = exports = function MetaPlugin(schema, options) {
  //  subDocument!
  schema.pre('save', function (next) {
    if (this.isNew) {
      this.meta = {} // undefined
      this.meta.createdAt = this.meta.updatedAt = Date.now()
    } else {
      // find 
      this.meta.updatedAt = Date.now()
    }

    next()
  })
}



